import { Component, h, Host, Prop, State } from '@stencil/core';

@Component({
    tag: 'ks-page-account-orders',
    styleUrl: 'orders.css'
})
export class PageAccountOrders {
    @Prop() heading: string = "Historia zamówień";
    @Prop() orderPrefix: string = "Nr";
    @Prop() accountLink: string;
    @Prop() accountButton: string = "Wróć do panelu klienta";

    @Prop() ordersJson: string = "";
    @State() orders: AccountOrder[];
    
    componentWillLoad() {
        this.orders = this.ordersJson ? JSON.parse(this.ordersJson) : [];
    }

	render() {
		return <Host>
            <h1>{this.heading}</h1>

            {this.orders.map(order => <a href={order.link} class="container">
                <div class="order">{this.orderPrefix} {order.order}</div>
                <div class="date">{order.date}</div>
                <div class="status"><div class="badge">{order.status}</div></div>
                <div class="value">{order.value}</div>
            </a>)}

            <ks-button rounded link={this.accountLink} name={this.accountButton} />
        </Host>;
	}
}


interface AccountOrder {
    link: string;
    order: string;
    date: string;
    status: string;
    value: string;
}