ks-page-account-orders {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1 1 auto;
    gap: 10px;
    padding: 40px 20px;
    
}

ks-page-account-orders .container {
    display: grid;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    align-items: baseline;

    max-width: 100%;
    width: 600px;
    padding: 30px;
    background-color: white;
    color: black;
    border: #ccc 1px solid;
    border-radius: 10px;
    box-sizing: border-box;

    position: relative;
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.0);
    transition: box-shadow ease 0.2s;
}

ks-page-account-orders .container:hover {
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

ks-page-account-orders .container:active {
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

ks-page-account-orders .order {
    font-size: 20px;
    font-weight: 700;
    font-family: var(--font-emphasis);
    padding-bottom: 5px;
}

ks-page-account-orders .value {
    text-align: right;
    font-size: 18px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

ks-page-account-orders .date {
    text-align: right;
}

ks-page-account-orders .badge {
    background-color: black;
    color: white;
    width: fit-content;
    padding: 3px 10px;
    border-radius: 5px;
}

ks-page-account-orders h1 {
    text-align: center;
    margin-bottom: 10px;
}

ks-page-account-orders ks-button {
    margin-top: 10px;
}


ks-page-account-orders .order {
    font-size: 20px;
    font-weight: 700;
    font-family: var(--font-emphasis);
    padding-bottom: 5px;
}

@media (max-width: 520px) {
    ks-page-account-orders .container {
        grid-template-columns: auto auto;
        grid-template-rows: auto auto auto;
        justify-content: center;
        gap: 0px 10px;

        padding: 20px;

        grid-template-areas: 
            "order order"
            "date date"
            "status value";
    }

    ks-page-account-orders .order { grid-area: order; text-align: center; }
    ks-page-account-orders .value { grid-area: value; }
    ks-page-account-orders .date { grid-area: date; text-align: center; padding-bottom: 20px; font-size: 14px; }
    ks-page-account-orders .status { grid-area: status; }
}